<template>
  <div>
    <h2>Skills</h2>
    <ul class="vertical-list">
      <li v-for="skill in skills" :key="skill.classification">
        <h4>{{ skill.classification }}</h4>
        <div v-for="item in skill.skill" :key="item">{{ item }}</div>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'MySkills',
  data() {
    return {
      isMobile: false,
      windowWidth: window.innerWidth,
      skills: [
        {
          "classification": "Front-End",
          "skill": ["HTML", "CSS", "JavaScript (React.js)"]
        },
        {
          "classification": "Back-End",
          "skill": ["Node.js (Express.js)", "TypeScript", "Python (Django, Flask)"]
        },
        {
          "classification": "Database",
          "skill": ["MySQL", "PostgreSQL", "SQLite", "MongoDB", "Prisma"]
        },
        {
          "classification": "Programming Language",
          "skill": ["C++", "Java", "Python"]
        },
        {
          "classification": "Machine learning",
          "skill": ["Python (Pytorch)", "TensorFlow", "Hugging Face", "Keras"]
        },
        {
          "classification": "DevOps",
          "skill": ["Unit Test (Jest)", "AWS (EC2, RDS)", "Nginx", "CI/CD", "Docker", "Git"]
        }
      ]
    }
  },
  mounted() {
    this.checkWidth();
    window.addEventListener('resize', this.checkWidth);
  },
  methods: {
    checkWidth() {
      this.isMobile = window.innerWidth <= 1000;
      this.windowWidth = window.innerWidth;
    }
  },
  beforeUnmount() {
      window.removeEventListener('resize', this.checkWidth);
  }

}
</script>

