<template>
  <div class="header" id="header">
    <div class="menu">
      <a href="#section1" class="scroll-to">About</a>
      <a href="#section2" class="scroll-to">Skills</a>
      <a href="#section4" class="scroll-to">Publications</a>
      <a href="#section3" class="scroll-to">Projects</a>
    </div>
  </div>
</template>

<script>
export default {
  name: 'MyHeader',
  mounted() {
    const header = document.getElementById('header');

    window.addEventListener('scroll', function() {
      if (window.scrollY > 100) {
        header.style.height = '60px';
      } else {
        header.style.height = '80px';
      }
    });
  }
}
</script>

