<template>
    <div>
        <img 
        title="Create by Adobe Firefly."
        alt="Vue logo" 
        src="../assets/logo.png" 
        style="border-radius: 50%; "
        @mouseover="playSound">
        <h1>It's Yu-Chieh Huang.</h1>
        <p>
          Software Engineer with 3 years of backend development experience and a strong background in AI research.
        </p>
    </div>
</template>

<script>
import meow from '../assets/meow.mp3'

export default {
  name: 'AboutMe',
  data() {
    return {
      isMobile: false,
      windowWidth: window.innerWidth,
      sound: null
    };
  },
  mounted(){
    this.checkWidth();
    window.addEventListener('resize', this.checkWidth);
  },
  methods: {
    playSound() {
      if (this.sound) {
        this.sound.pause();
        this.sound.currentTime = 0;
      }
      this.sound = new Audio(meow);
      this.sound.play();
    },
    checkWidth() {
      this.isMobile = window.innerWidth <= 550;
      this.windowWidth = window.innerWidth;
    }
    },
    beforeUnmount() {
        window.removeEventListener('resize', this.checkWidth);
    }
  }
</script>

<style scoped>
    img {
      transition: filter 0.3s;
    }

    img:hover {
      filter: brightness(70%);
    }
    @media screen and (max-width: 550px) {
        img {
           width:82%;
        }  
        h1  {
           font-size: 25px;
        }
    }
</style>