<template>
  <div id="app">
    <MyHeader />
    <div id="section1" style="padding: 70px; position: relative;">
      <MainPage />
    </div>
    <MyFooter />
  </div>
</template>

<script>
import MainPage from './views/MainPage.vue'
import MyHeader from './components/Header.vue'
import MyFooter from './components/Footer.vue'
import meow from './assets/meow.mp3'

export default {
  name: 'App',
  components: {
    MainPage,
    MyHeader,
    MyFooter
  },
  data() {
    return {
      sound: null
    };
  },
  methods: {
    playSound() {
      if (this.sound) {
        this.sound.pause();
        this.sound.currentTime = 0;
      }
      this.sound = new Audio(meow);
      this.sound.play();
    }
  }
}
</script>

